#bgvideo {
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	padding: 0;
	min-width: 99.999%;
	min-height: 550px;
	z-index: 0
}

#video-btn,#video-add {
	position: absolute;
	bottom: 3px;
	right: 5px;
	width: 32px;
	height: 32px;
	z-index: 7;
	background-position: center;
	background-size: cover;
	cursor: pointer;
	opacity: .8;
	-moz-animation: poi-face 10s linear infinite alternate;
	-webkit-animation: poi-face 10s linear infinite alternate;
	-o-animation: poi-face 10s linear infinite alternate;
	animation: poi-face 10s linear infinite alternate
}

#video-btn:hover,#video-add:hover {
	opacity: 1
}

.video-play,.loadvideo {
	background-image: url('https://cdn.jsdelivr.net/gh/LIlGG/cdn@1.0.8/img/Sakura/images/play@32x32.png')
}

.video-pause {
	background-image: url('https://cdn.jsdelivr.net/gh/LIlGG/cdn@1.0.8/img/Sakura/images/pause@32x32.png')
}

#video-add {
	background-image: url('https://cdn.jsdelivr.net/gh/LIlGG/cdn@1.0.8/img/Sakura/images/add@32x32.png');
	bottom: 45px;
	display: none
}

.video-stu {
	position: absolute;
	bottom: -100px;
	left: 0;
	right: 0;
	margin: auto;
	padding: 6px 15px;
	text-align: center;
	color: #fff;
	width: 100%;
	background-color: rgba(0,0,0,.8);
	border-radius: 0;
	font-size: 18px;
	-webkit-transition: .4s ease all;
	-moz-transition: .4s ease all;
	-o-transition: .4s ease all;
	transition: .4s ease all
}

.pattern-center {
	position: relative;
	top: 0;
	left: 0;
	width: 100%;
	overflow: hidden
}

.pattern-center:before {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0,0,0,.3)
}

.pattern-center:after {
	content: '';
	width: 150%;
	height: 4.375rem;
	background: #fff;
	left: -25%;
	bottom: -2.875rem;
	border-radius: 100%;
	position: absolute
}

.pattern-attachment-img {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	background-origin: border-box;
	width: 100%;
	height: 400px
}

.pattern-attachment-img img {
	width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.pattern-center header.pattern-header {
	position: absolute;
	top: 45%;
	left: 0;
	right: 0;
	text-align: center;
	color: #fff;
	text-shadow: 2px 2px 10px #000;
	z-index: 1
}

.pattern-center h1.cat-title,.pattern-center h1.entry-title {
	color: #fff;
	font-size: 40px;
	font-weight: 500;
	width: 80%;
	margin: auto;
	padding: 0;
	border: 0
}

.pattern-center .cat-des {
	font-size: 16px;
	display: block
}

.single-center:after {
	display: none
}

.pattern-center header.single-header {
	max-width: 800px;
	padding: 0 10px;
	margin-left: auto;
	margin-right: auto;
	text-align: left;
	top: auto;
	bottom: 20px
}

.single-center .single-header h1.entry-title {
	margin: 0;
	font-size: 32px;
	width: 100%
}

.single-center .entry-census {
	color: #fff;
	font-size: 14px;
	padding: 18px 0 0;
	line-height: 39px
}

.single-center .entry-census .bull {
	margin: 0 5px
}

.single-center .entry-census span img {
	width: 35px;
	height: 35px;
	border-radius: 100%;
	float: left;
	margin-right: 12px
}

.single-center .entry-census a {
	color: #fff
}

@media (max-width:860px) {
	.pattern-center header.single-header {
		padding: 0 4%;
		top: 45%;
	}

	.pattern-attachment-img {
		height: 280px
	}

	.pattern-center h1.cat-title,.pattern-center h1.entry-title,.single-center .single-header h1.entry-title {
		font-size: 26px
	}

	.single-center .single-header h1.entry-title {
		text-align: center;
	}

}

.pattern-center-sakura {
	position: relative;
	top: 0;
	left: 0;
	width: 800px;
	overflow: hidden;
	margin: 0 auto
}

.pattern-center-blank {
