.site-top ul li {
	float: left;
	margin: 0 13px;
	position: relative;
	transition: all 1s ease;
	-webkit-transition: all 1s ease
}

.site-top ul li a {
	padding: 10px 0;
	display: inline-block;
	color: #666
}

.site-top ul li a:hover {
	color: orange
}

.site-top ul li a:after {
	content: "";
	display: block;
	position: absolute;
	bottom: -16px;
	height: 6px;
	background-color: #fe9600;
	width: 100%;
	max-width: 0;
	transition: max-width .25s ease-in-out
}

.site-top ul li ul li a:after {
	content: "";
	display: none;
	position: absolute;
	bottom: -21px;
	height: 6px;
	background-color: #fe9600;
	width: 100%;
	max-width: 0;
	transition: max-width .25s ease-in-out
}

.site-top ul li a:hover:after {
