#show-nav {
	position: relative;
	float: right;
	margin-left: 20px;
	display: block;
	width: 30px;
	height: 33px;
	margin-top: 4.5px;
	cursor: pointer;
	z-index: 999
}

#show-nav .line {
	position: absolute;
	top: 7px;
	left: 50%;
	width: 28px;
	margin-left: -15px;
	height: 2px;
	background: #666;
	transition: all .2s ease;
	-webkit-transition: all .2s ease
}

#show-nav.showNav .line1 {
	top: 16px
}

#show-nav.showNav .line3 {
	top: 25px
}

#show-nav.hideNav .line1 {
	top: 16px;
	transform: rotateZ(45deg);
	-webkit-transform: rotateZ(45deg)
}

#show-nav.hideNav .line2 {
	opacity: 0
}

#show-nav.hideNav .line3 {
	top: 16px;
	transform: rotateZ(-45deg);
	-webkit-transform: rotateZ(-45deg)
