/** avatar */
.journal-author-img .avatar {
	border: 1px solid #ddd;
    padding: 2px;
    float: left;
    border-radius: 64px;
	transition: all 1.0s;
}

.journal-author-img .avatar {
	border-radius:100%;
	-moz-border-radius:100%;
	box-shadow:inset 0 -1px 0 3333sf;
	-webkit-box-shadow:inset 0 -1px 0 3333sf;
	-webkit-transition:0.4s;
	-webkit-transition:-webkit-transform 0.4s ease-out;
	transition:transform 0.4s ease-out;
	-moz-transition:-moz-transform 0.4s ease-out;
}
.journal-author-img .avatar:hover {
	-webkit-transform:rotateZ(360deg);
	-moz-transform:rotateZ(360deg);
	-o-transform:rotateZ(360deg);
	-ms-transform:rotateZ(360deg);
	transform:rotateZ(360deg);
}

.journals-content {
	padding: 10px;
}

ul.journals-line {
	padding: 0;
	margin: 30px 0 0 0;
    list-style: none;
    display: inline;
    position: relative;
}

.journal-label:hover {
	/* transform:scale(1.05);*/
	transform:translateY(-3px);
	z-index:1;
	box-shadow:0 15px 32px rgba(0,0,0,0.15) !important
}

.journals-line>li:nth-child(odd) .journal-label {
    background: #E6E6FA;
	color: black;
}

.journals-line>li:nth-child(odd) .journal-label:after {
    border-right-color: #E6E6FA;
	color: black;
}

.journals-line>li .journal-label {
	margin: 0 0 45px 65px;
    z-index: 1;
    background: #F0FFFF;
    color: black;
    padding: .8em 1.2em .4em 1.2em;
    font-size: 1.2em;
    font-weight: 300;
    line-height: 1.4;
    position: relative;
    border-radius: 5px;
    transition: all 0.3s ease 0s;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    display: block;
}

.journals-line>li .journal-label:after {
    right: 100%;
    border: solid transparent;
    z-index: -1;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-right-color: #F0FFFF;
    border-width: 10px;
    top: 4px;
}

